import React, {Component}  from "react";


import { Link } from "react-router-dom"; 



export default class Employees extends Component{
    
    constructor(props){
        super(props);
    
        this.state={         
         
           currentPage: 1,
           EmployeesPerPage: 5,

           EmployeeIdFilter:"",
           EmployeeNameFilter:"",
           EmployeesWithoutFilter:[]
        }      
       
        this.handleClick = this.handleClick.bind(this);
        this.renderNumbers = this.renderNumbers.bind(this);
       
        this.OnDelete = this.OnDelete.bind(this);
        this.OnEdit = this.OnEdit.bind(this);
     
        this.sortResult = this.sortResult.bind(this);
       
        this.filterChosenData = this.filterChosenData.bind(this);

    }
    
 
    handleClick(event) {
        this.setState({
          currentPage: Number(event.target.id)
        });
      }
    

    renderNumbers(){
        const {EmployeesPerPage, currentPage } = this.state;
        const {Employees_two} = this.props;
        const pageNumbers = [];
        const max = Math.ceil(Employees_two.length / EmployeesPerPage);
        let flagg= true;
        for (let i = 1; i <= max; i++) {
            if(i===1 || i===max || i===currentPage || i===(currentPage+1) 
            || i===(currentPage-1)|| i===(currentPage+2) || i===(currentPage-2))
            {
                pageNumbers.push(i);
                flagg=true;
            }
            else{
                if(flagg){
                pageNumbers.push("...");
                flagg=false;
            }
            }           
        }

        const renderPageNumbers = pageNumbers.map(number => {
            if(Number.isInteger( number)){
        return (         
          <span
            key={number}
            id={number}
            onClick={this.handleClick}
          >           
             [{number}]     
          </span>
        );
      }
      else{
        return (         
            <span
              key={number}
              id={number}
              onClick={this.handleClick}
            >           
               {number}    
            </span>
          )
      }});     
      return renderPageNumbers

    }

    OnDelete(id){
       
        this.props.OnDelete(id);
     //   console.log(this.props.Employees_two);
     
    }

    OnEdit(id, body){
        this.props.onEdit(id, body);
    }

   
    sortResult(prop,asc){
        this.props.SortData(prop,asc);       
    }

    

    filterChosenData(name_field){        

        return(
            <div className="d-flex flex-row">   
            <label className="mt-2">  {name_field}</label>
            <button type="button" className="btn btn-light"
            onClick={()=>this.sortResult(name_field, true)}>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-down-square-fill" viewBox="0 0 16 16">
                <path d="M2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2zm6.5 4.5v5.793l2.146-2.147a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 1 1 .708-.708L7.5 10.293V4.5a.5.5 0 0 1 1 0z"/>
                </svg>
            </button>

            <button type="button" className="btn btn-light"
            onClick={()=>this.sortResult(name_field, false)}>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-up-square-fill" viewBox="0 0 16 16">
                <path d="M2 16a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H2zm6.5-4.5V5.707l2.146 2.147a.5.5 0 0 0 .708-.708l-3-3a.5.5 0 0 0-.708 0l-3 3a.5.5 0 1 0 .708.708L7.5 5.707V11.5a.5.5 0 0 0 1 0z"/>
                </svg>
            </button>

            </div>
        )
    }

    getRenderTime(date_new){
return date_new.getMonth();
    }
    

    render(){
        let Employees = this.props.Employees_two;
       

        console.log("props")
        console.log(Employees);
       
        let { currentPage, EmployeesPerPage } = this.state;

        //Догика страниц
            const indexOfLastEmployee = currentPage * EmployeesPerPage;
            const indexOfFirstEmployee = indexOfLastEmployee - EmployeesPerPage;
            const currentEmployees = Employees.slice(indexOfFirstEmployee, indexOfLastEmployee);
           
        return (
        <div>   
            <h1 className="d-flex justify-content-center m-3"> Список сотрудников</h1>      
                    
                    <Link  className='btn btn-primary m-2 float-end' variant='primary'
                    to="/addemployee">
                    Add Employee</Link>                  
                

            <table className="table table-striped">
            <thead>
            <tr>
               
                <th>    
                {this.filterChosenData("EmployeeName")}                                  
                </th>
                <th>    
                {this.filterChosenData("EmployeeEmail")}                                  
                </th>
                <th>    
                {this.filterChosenData("EmployeeSalary")}                                  
                </th>
                <th>    
                {this.filterChosenData("EmployeeBirthday")}                                  
                </th>

                <th>
                    Options
                </th>
            </tr>
            </thead>
            <tbody>
                {currentEmployees.map(dep=>
                    <tr key={dep.EmployeeId}>                      
                        <td>{dep.EmployeeName}</td>
                        <td>{dep.EmployeeEmail}</td>
                        <td>{dep.EmployeeSalary}</td>
                        <td>{ dep.EmployeeBirthday}</td>

                        <td className="d-grid gap-2 d-md-block">
                        <Link type="button"  
                        style={{width: '110px'}}
                        className="btn btn-primary me-md-4"                       
                        to={`/${dep.EmployeeId}`}
                        key={dep.EmployeeId}>
                             <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pencil-square rounded float-left" viewBox="0 0 16 16" >
                            <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                            <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                           
                            </svg>
                              Edit   
                        </Link>
        
                        <button type="button"
                        style={{width: '110px'}}
                        className="btn btn-danger "
                        onClick={()=>this.OnDelete(dep.EmployeeId)}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash-fill" viewBox="0 0 16 16">
                            <path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z"/>
                            </svg>
                            Delete
                        </button>
                            
                        </td>
                    </tr>
                    )}
            </tbody>
            </table>

            <tr id="page-numbers">
         <td> {this.renderNumbers()} </td>
         </tr>

         <Link type="button"  
                        style={{width: '110px'}}
                        className="btn btn-primary btn-md "                       
                        to={"/login"}
                        >
                             
                              Edit   
                        </Link>

        </div>  

        );      
    }
}