import React, {Component} from 'react';
import { Link,  } from "react-router-dom"; 


export class LoginPage extends Component{
    constructor(props){
        super(props);
        this.state = {          

            etalon_login: 'admin',
            etalon_parole: 'admin',

            
            login: '',
            parole: '',

            is_logged: false,

            adres: '/login'

        
        
        }
        this.handleSubmit=this.handleSubmit.bind(this);
        this.onValueChange_login = this.onValueChange_login.bind(this); 
        this.onValueChange_parole = this.onValueChange_parole.bind(this);       
    }

   
    
     handleSubmit(event){
        event.preventDefault();
        if(this.state.etalon_login===this.state.login && this.state.etalon_parole===this.state.parole){
            this.setState(
                {
                    adres: '/'
                   
                });
        }else{
            this.setState(
                {
                    adres: '/login'
                   
                });
        }
         
       
           
     
    }

    
    onValueChange_login = (e) => {
        this.setState(
            {
                login: e.target.value
            });
       
        console.log(this.state.login);
    };

    onValueChange_parole = (e) => {
        this.setState(
            {
                parole: e.target.value
            });
       
        console.log(this.state.parole);
    };


   


    render(){
        return (
            <div className="container">
                 <h2 className="d-flex justify-content-center m-3"> Вход</h2> 
               <br></br> 

            <form 
            className="bottom-panel d-flex justify-content-center">
                <h4 htmlFor='login_text'>Login 
                <h4> </h4> 
            <input
                type="text"
                name="login_text"  
                placeholder="Заполните" required
                className="form-control new-post-label"   
                style={{width: '300px'}} 
                onChange={this.onValueChange_login}
            />
             </h4>

             </form>
             <br></br> 


             <form 
            className="bottom-panel d-flex justify-content-center">
             <h4 htmlFor='parole_text'>Parole 
                <h4> </h4> 
            <input
                type="text"
                name="parole_text"  
                placeholder="Заполните" required
                className="form-control new-post-label"   
                style={{width: '300px'}} 
                onChange={this.onValueChange_parole}
            />
             </h4>

            </form>
            <div>
            <br></br> 
               
            <Link
                          
               
                 to={this.state.adres}>
                   <button type="submit"
                className="btn btn-primary m-1 "    
                style={{width: '200px'}} 
                > Log in</button> 
                  
            </Link>           
            </div>
        </div>

        )
    }

}
