import React,{Component} from 'react';
import { Link } from "react-router-dom"; 
import DatePicker from "react-datepicker";

export class AddEmplModal extends Component{
    constructor(props){
        super(props);
        this.state = {
            fields: {},
            errors: {},           
        }
     

       
    }

    onValueChange(field, e) {
        let fields = this.state.fields;
        fields[field] = e.target.value;
        this.setState({ fields });
     }
    
     handleSubmit(event){
        event.preventDefault();
        if (this.handleValidation()) {

            this.props.onAdd(this.state.fields);   
            alert("Записано");
          } else {
          //  alert("Form has errors.");
          }
               
    }

    handleValidation() {
        let fields = this.state.fields;
        let errors = {};
        let formIsValid = true;
    
        //Name
        if (!fields["name"]) {
          formIsValid = false;
          errors["name"] = "Cannot be empty";
        }
    
        if (typeof fields["name"] !== "undefined") {
          if (!fields["name"].match(/^[a-zA-Z]+$/)) {
            formIsValid = false;
            errors["name"] = "Only letters";
          }
        }

         //Email
         if (!fields["email"]) {
          formIsValid = false;
          errors["email"] = "Cannot be empty";
        }
    
        if (typeof fields["email"] !== "undefined") {
          let lastAtPos = fields["email"].lastIndexOf("@");
          let lastDotPos = fields["email"].lastIndexOf(".");
    
          if (
            !(
              lastAtPos < lastDotPos &&
              lastAtPos > 0 &&
              fields["email"].indexOf("@@") === -1 &&
              lastDotPos > 2 &&
              fields["email"].length - lastDotPos > 2
            )
          ) {
            formIsValid = false;
            errors["email"] = "Email is not valid";
          }
        }

          //Salary
          if (!fields["salary"]) {
            formIsValid = false;
            errors["salary"] = "Cannot be empty";
          }

          if (typeof fields["salary"] !== "undefined") {
            if (!fields["salary"].match(/^[0-9.,]+$/)) {
              formIsValid = false;
              errors["salary"] = "Only numbers";
            }
          }

          if(fields["salary"]<0){
            
              formIsValid = false;
              errors["salary"] = "Salary is not valid";
            
          }
                
        this.setState({ errors: errors });
        return formIsValid;
      }

      form_single_input(tag_field, name_field){
        return (
          <form 
          className="bottom-panel d-flex justify-content-center">    
              <h4 htmlFor='Employee'>{tag_field} employee 
              
                  <input
                  type="text"
                  placeholder="Заполните"
                  name='Employee'
                  className="form-control new-post-label justify-content-center"
                  style={{width: '500px'}}
                  onChange={this.onValueChange.bind(this, tag_field)}
                  value = {this.state.fields[tag_field]}
                   />
                  <label style={{ color: "red" }}>{this.state.errors[tag_field]}</label>
          </h4>
      </form>

        )
      }

      form_date_input(tag_field, name_field){
   //     let now = new Date();
        return (
          <form 
          className="bottom-panel d-flex justify-content-center">    
              <h4 htmlFor='Employee'>{tag_field} employee 
                
             
          </h4>
              <DatePicker 
                todayButton={"Today"}
                dateFormat="DD/MM/YYYY"
             
              onChange={this.onValueChange.bind(this, tag_field)} />

                 
                  <label style={{ color: "red" }}>{this.state.errors[tag_field]}</label>

      </form>

        )
      }
      

   

    render(){
      // {this.form_date_input("birthday", "EmployeeSalary")} npm audit fix --force
   //   let now = new Date();
        return (
        <div className="container">
               <h2 className="d-flex justify-content-center m-3"> Добавить сотрудника</h2> 
               <br></br> 
        
               {this.form_single_input("name", "EmployeeName")}
               {this.form_single_input("email", "EmployeeEmail")}
               {this.form_single_input("salary", "EmployeeSalary")}
               <DatePicker 
            //  selected={now} 
              onChange={this.onValueChange.bind(this, "birthday")} />



            <div>
            <br></br>         

            <form 
            className="bottom-panel d-flex justify-content-center"
             >
            <button
                type="submit"
                className="btn btn-primary m-2 "
                onClick={this.handleSubmit.bind(this)}>
                    Add Employee
            </button>

            <Link
                type="danger"
                className="btn btn-danger m-2 float-end"
                to="/">  
                              
                    Close
            </Link>
            </form>
            </div>
        

               
        </div>

        )
    }

}