import React, { useState} from 'react';
import { Link,  } from "react-router-dom"; 
import { useParams } from "react-router";


   
const EditEmplModal = ({OnEdit, Employees_two})=>{   
    const { EmployeeId } = useParams();
    const [fields, refreshfields] = useState({});
    const [errors, refresherrors] = useState({});
    let counter=0;
        
    function reset_old(){
      let fieldsr = fields;
      fieldsr["name"] = Employees_two[EmployeeId-1].EmployeeName;
      fieldsr["email"] = Employees_two[EmployeeId-1].EmployeeEmail;
      fieldsr["salary"] = Employees_two[EmployeeId-1].EmployeeSalary;

      refreshfields(fieldsr);
    }
      
    function handleChange ( field, e) {

      if(counter<1){
        reset_old();
        console.log("lalalala");
      }

      let fieldsn = fields;
      fieldsn[field] = e.target.value;
    
  

            refreshfields(fieldsn);
            counter++;
            console.log(fields);
            console.log(counter);
          
    }


    let handleSubmit = (e)=>{
      if(counter<1){
        reset_old();
        console.log("hohoho");
      }
            e.preventDefault();   
            console.log(EmployeeId);
          

          if (handleValidation()) {
            OnEdit(EmployeeId, fields);    
             alert("Записано");
          }else{
          }   
            
        }

        
    function handleValidation() {
        let fields_new = fields;
        let errors_new = {};
        let formIsValid = true;
    
        //Name
        if (!fields_new["name"]) {
          formIsValid = false;
          errors_new["name"] = "Cannot be empty";
        }
    
        if (typeof fields_new["name"] !== "undefined") {
          if (!fields_new["name"].match(/^[a-zA-Z]+$/)) {
            formIsValid = false;
            errors_new["name"] = "Only letters";
          }
        }

        //Email
        if (!fields_new["email"]) {
          formIsValid = false;
          errors_new["email"] = "Cannot be empty";
        }
    
        if (typeof fields_new["email"] !== "undefined") {
          let lastAtPos = fields_new["email"].lastIndexOf("@");
          let lastDotPos = fields_new["email"].lastIndexOf(".");
    
          if (
            !(
              lastAtPos < lastDotPos &&
              lastAtPos > 0 &&
              fields["email"].indexOf("@@") === -1 &&
              lastDotPos > 2 &&
              fields["email"].length - lastDotPos > 2
            )
          ) {
            formIsValid = false;
            errors_new["email"] = "Email is not valid";
          }
        }

           //Salary
           if (!fields["salary"]) {
            formIsValid = false;
            errors_new["salary"] = "Cannot be empty";
          }

          if (typeof fields["salary"] !== "undefined") {
            if (isNaN( fields["salary"])) {
              formIsValid = false;
              errors_new["salary"] = "Only numbers";
            }
          }

          if(fields["salary"]<0){
            
              formIsValid = false;
              errors_new["salary"] = "Salary is not valid";
            
          }
        refresherrors(errors_new);
       
        console.log(formIsValid);
        console.log(fields["salary"]);
        console.log(errors["salary"]);
        return formIsValid;
      }


     function form_single_input(tag_field, old_data){
     
      return (
          <form 
          className="bottom-panel d-flex justify-content-center">              
              <h4 htmlFor='Employee'>{tag_field} employee 
             
          <input
              type="text"
              style={{width: '500px'}}
              placeholder="Заполните" required
              className="form-control new-post-label"    
              onChange={
                handleChange.bind(this, tag_field)  }
              defaultValue={old_data}
             
          />
           <label style={{ color: "red" }}>{errors[tag_field]}</label>
           </h4>
      </form>

        )
      }
  

        return (
            //, Employees_two[EmployeeId-1].EmployeeSalary
        <div className="container">

               <h2 className="d-flex justify-content-center m-3"> Редактировать сотрудника</h2> 
               <br></br> 

               {form_single_input("name", Employees_two[EmployeeId-1].EmployeeName)}
               {form_single_input("email", Employees_two[EmployeeId-1].EmployeeEmail)}
               {form_single_input("salary", Employees_two[EmployeeId-1].EmployeeSalary)}

            
            <br></br> 

            <div>
            <form 
            className="bottom-panel d-flex justify-content-center"
             >

            <button
                type="submit"
                className="btn btn-primary m-2 "                
                onClick={handleSubmit}>
                    Edit Employee
            </button>

            <Link
                type="danger"
                className="btn btn-danger m-2 float-end"
                to="/">  
                              
                    Close
            </Link>
            </form>
            </div>
        

               
        </div>

        )
    
}

export default EditEmplModal;