addItem(body){
  let {name, email, salary, birthday} = body
  const newItem = {
      EmployeeName: name,
      EmployeeEmail:email,
      EmployeeSalary: salary,  
      EmployeeBirthday: birthday,  
      EmployeeId: this.maxId++
  }

  this.setState(({Employees}) =>{

      const newArr = [...Employees, newItem];
      return {
        Employees: newArr
      }
    }) 
  }
  EditData(id, body){
    let {name, email, salary} = body
    const newItem = {
      EmployeeName: name,
      EmployeeEmail:email,
      EmployeeSalary: salary,    
      EmployeeId: id
  }
 
    this.setState(({Employees}) => {
   
      Employees[id-1] = newItem;   
      return {
        Employees
      }
    });
  }

  DeleteData(id){
   
    this.setState(({Employees}) => {

      const index = Employees.findIndex(elem => elem.EmployeeId === id);
      const newArr = [...Employees.slice(0, index), ...Employees.slice(index+1)];
      return {
        Employees: newArr
      }
  });

  }



  SortData(prop,asc){
   
    this.setState(({Employees}) => {

     let sortedData=Employees.sort(function(a,b){
        if(asc){
            return (a[prop]>b[prop])?1:((a[prop]<b[prop])?-1:0);
        }
        else{
            return (b[prop]>a[prop])?1:((b[prop]<a[prop])?-1:0);
        }
    });

      return {
        Employees: sortedData
      }
  });

          
   
}



